.logo-circle path {
  fill: transparent;
  stroke-width: 0;
  stroke: #39b1be;
  stroke-dasharray: 147;
  stroke-dashoffset: 0;
  animation: logo 2s forwards; }

@keyframes logo {
  0% {
    stroke-dashoffset: 0;
    stroke-width: 0; }
  1% {
    stroke-dashoffset: 147;
    stroke-width: 0; }
  50% {
    stroke-dashoffset: 294;
    fill: transparent;
    stroke-width: 2; }
  100% {
    stroke-dashoffset: 294;
    fill: #39b1be; } }

.logo-circle svg {
  width: 47px;
  height: 47px; }
  @media (max-width: 479px) {
    .logo-circle svg {
      width: 30px;
      height: 30px; } }
  .active .logo-circle svg path {
    fill: #fff;
    animation: logo-active .5s forwards; }

@keyframes logo-active {
  0% {
    fill: #39b1be; }
  100% {
    fill: #fff; } }

.logo-text1, .logo-text2 {
  color: #39b1be;
  transition: color .5s ease 0s; }
  .active .logo-text1, .active .logo-text2 {
    color: #fff;
